.lmui-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  background: #000;
}
.lmui-overlay-enter {
  -webkit-animation: lmui-overlay-in 0.3s ease;
          animation: lmui-overlay-in 0.3s ease;
}
.lmui-overlay-leave {
  -webkit-animation: lmui-overlay-out 0.3s ease forwards;
          animation: lmui-overlay-out 0.3s ease forwards;
}
@-webkit-keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@-webkit-keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
@keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
.lmui-spin {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  text-align: center;
  background-color: rgba(255, 255, 255, .5);
}
.lmui-spin .lmui-spin-container {
  position: absolute;
  width: 50px;
  height: 50px;
  margin-top: -20px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.lmui-spin .lmui-spin-container .loadding {
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
}
.lmui-spin .lmui-spin-container .loadding circle {
  fill: none;
  stroke: #4990e2;
  stroke-dasharray: 450, 150;
  stroke-dashoffset: 150;
  -webkit-animation: dash 2s ease-in-out infinite;
          animation: dash 2s ease-in-out infinite;
  stroke-linecap: round;
  stroke-width: 8;
  stroke-miterlimit: 10;
}
.lmui-spin .lmui-spin-container .loadding .right {
  stroke: #fff;
  stroke-linecap: round;
  stroke-width: 10px;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
  opacity: 0;
}
.lmui-spin .lmui-spin-container .loadding .wrong {
  stroke: #fff;
  stroke-linecap: round;
  stroke-width: 10px;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
  opacity: 0;
}
@-webkit-keyframes rotate {
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
@keyframes rotate {
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
.lmui-spin .success .loadding {
  -webkit-animation: none;
          animation: none;
}
.lmui-spin .success .loadding circle {
  fill: #0ba26b;
  stroke: #0ba26b;
  stroke-dasharray: 0;
  -webkit-animation: none;
          animation: none;
}
.lmui-spin .success .loadding .right {
  opacity: 1;
}
.lmui-spin .fail .loadding {
  -webkit-animation: none;
          animation: none;
}
.lmui-spin .fail .loadding circle {
  fill: #ff554b;
  stroke: #ff554b;
  stroke-dasharray: 0;
  -webkit-animation: none;
          animation: none;
}
.lmui-spin .fail .loadding .wrong {
  opacity: 1;
}
.lmui-spin .lmui-spin-text {
  position: absolute;
  top: 50%;
  width: 100%;
  padding-top: 25px;
}
